import React, { useState, useEffect } from 'react';
import dayjs from 'dayjs';
import './Time.css';



const Time = () => {


  const [intervalTime, setIntervalTime] = useState({});
  const [nowTime, setNowTime] = useState();

  // 当前时间
  const nowTimeFun = () => {
    let result = dayjs().format('YYYY-MM-DD HH:mm:ss');
    return result;
  }

  const IntervalTime = (now) => {
    // const date1 = dayjs('2021-01-25')
    // const date2 = dayjs('2018-07-02')
    // date1.diff(date2)    

    const date1 = dayjs(now);
    let confirmDay = date1.diff('2020-11-22', 'day');
    let confirmWeek = date1.diff('2020-11-22', 'week');
    let confirmYear = (date1.diff('2020-11-22', 'year', true)).toFixed(1);

    let meetDay = date1.diff('2020-11-07', 'day');


    setIntervalTime({
      confirmDay,
      confirmWeek,
      confirmYear,
      meetDay
    });
  }

  useEffect(() => {
    setInterval(() => {
      let now = dayjs()
      IntervalTime(now);
      let result = nowTimeFun();
      setNowTime(result);

    }, 1000);
  }, [])

  const movieList = [
    { id: '1', name: "《 风平浪静 》", date: "2020-11-07", cinema: "" },
    { id: '2', name: "《 一秒钟 》", date: "2020-11-29", cinema: "" },
    { id: '3', name: "《 送你一朵小红花 》", date: "2021-01-02", cinema: "" },
    { id: '4', name: "《 心灵奇旅 》", date: "2021-01-17", cinema: "" },
    { id: '5', name: "《 缉魂 》", date: "2021-02-09", cinema: "" },
    { id: '6', name: "《 你好，李焕英 》", date: "2021-02-14", cinema: "" },
    { id: '7', name: "《 寻龙传说 》", date: "2021-03-13", cinema: "" },
    { id: '8', name: "《 哥斯拉大战金刚 》", date: "2021-03-27", cinema: "" },
    { id: '9', name: "《 我的姐姐 》", date: "2021-04-03", cinema: "" },
    { id: '10', name: "《 名侦探柯南：绯色的子弹 》", date: "2021-04-17", cinema: "" },
    { id: '11', name: "《 你的婚礼 》", date: "2021-05-03", cinema: "" },
    { id: '12', name: "《 情书 》", date: "2021-05-23", cinema: "" },
    { id: '13', name: "《 当男人恋爱时 》", date: "2021-06-27", cinema: "" },
    { id: '14', name: "《 比得兔2：逃跑计划 》", date: "2021-07-03", cinema: "" },
  ]

  return (
    <>
      <div style={{ margin: '0 auto', border: '1px solid black', width: '20%', textAlign: 'center' }}>
        <h3>当前时间:</h3>
        <p>{nowTime}</p>
        <h3>距首次见面已:</h3>
        <p>{intervalTime.meetDay}天</p>
        <h3>距正式确认已:</h3>
        <p>{intervalTime.confirmDay}天</p>
        <p>{intervalTime.confirmWeek}周</p>
        <p>{intervalTime.confirmYear}年</p>
      </div>

      <div>
        <table className="film-table">
          <tr>
            <th>序号</th>
            <th>电影名称</th>
            <th>日期</th>
            <th>影院</th>
          </tr>

          {movieList.map(item => {
            return (
              <>
                <tr>
                  <td>{item.id}</td>
                  <td>{item.name}</td>
                  <td>{item.date}</td>
                  <td>{item.cinema}</td>
                </tr>
              </>
            )
          })}

        </table>
      </div>
    </>
  )
}

export default Time;